.coin {
  display: inline-block;
  border: 0.2em solid;
  border-radius: 0.4em;
  -webkit-box-shadow: 0.4em 0.1em 1em rgba(0,0,0,0.3);
          box-shadow: 0.4em 0.1em 1em rgba(0,0,0,0.3);
  padding: 0.6em;
  margin: 1em;
  cursor: pointer;
  -webkit-transition: -webkit-box-shadow 0.3s ease-in-out;
  transition: -webkit-box-shadow 0.3s ease-in-out;
  -o-transition: box-shadow 0.3s ease-in-out;
  transition: box-shadow 0.3s ease-in-out;
  transition: box-shadow 0.3s ease-in-out, -webkit-box-shadow 0.3s ease-in-out;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  min-width: 10em;
}
.coin:active, .coin:hover, .coin.active {
  -webkit-box-shadow: 0.2em 0.05em 1em rgba(0,0,0,0.3);
          box-shadow: 0.2em 0.05em 1em rgba(0,0,0,0.3);
}
.coin svg {
  -webkit-filter: drop-shadow(0.2em 0.2em 0.2em rgba(0,0,0,0.3));
          filter: drop-shadow(0.2em 0.2em 0.2em rgba(0,0,0,0.3));
}

.coin-seperator {
  margin-bottom: 0.4em;
}

.coin-details {
  margin-top: 0.4em;
  color: #333333;
}

.coin-details p {
  margin: 0;
}

.coin-name {
  font-size: 1.4em;
  font-weight: bold;
}

.trend {
  min-width: 30em;
  max-width: 100%;
}

@media screen and (max-width: 32em) {
  .trend {
    display: block;
    min-width: 100%;
    margin: 1em 0;
  }
}



